.topbarContainer {
  height: 60px;
  width: 100%;
  background-color: #1877f2;
  display: flex;
  align-items: center;
  top: 0;
  position: sticky;
  z-index: 1;
}
.topbarLeft {
  flex: 3;
}
.logo {
  font-size: 24px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  margin-left: 15px;
}

.topbarCenter {
  flex: 5;
}

.searchBar {
  width: 100%;
  height: 35px;
  background-color: white;
  border-radius: 2px;
  display: flex;
  align-items: center;
}

.searchIcon {
  font-size: 20px !important;
  margin-left: 10px;
}

.searchInput {
  border: none;
  width: 70%;
}
.searchInput:focus {
  outline: none;
}
.topbarRight {
  flex: 4;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: white;
}

.topbarLink {
  margin-right: 10px;
  font-size: 14px;
  cursor: pointer;
  /* font-family: 'Cinzel', serif;
    font-weight: bold; */
}
.topbarIcons {
  display: flex;
}
.topbarIconItem {
  margin-right: 15px;
  cursor: pointer;
  position: relative;
}

.topbarIconBadge{
    width: 15px;
    height: 15px;
    background-color: red;
    border-radius: 50%;
    color: white;
    position: absolute;
    top: -5px;
    right: -5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}

.topbarImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  background-color: violet;
  cursor: pointer;
}
