.rightbarFriend {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.profileContainer {
    margin-right: 10px;
    position: relative;
}

.rightbarProfileImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.rightbarOnline {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: limegreen;
    position: absolute;
    /* top: -2; */
    right: 0;
    border: 2px solid white;

}

.username{
    font-size: 15px;
    font-family: 'Wendy One', sans-serif;
    /* font-weight: bold; */
}